.carousel__item {
	width: 300px;
	height: auto;
	margin: 0 auto;
	padding: 30px;
	text-align: center;
}

.carousel__item > img {
	width: 100%;
	height: 100%;
}

.carousel__item > h4 {
	color: #f00;
}

.carousel__item > button {
	padding: 10px;
	background-color: #43abdc;
	border: none;
	border-radius: 5px;
}

.carousel__item > button > a {
	font-weight: bold;
	color: #fff;
	text-decoration: none;
}
